.navbar {
    background: url(/jie-bakery-website/static/media/home-page-bg.96782d91.jpg);
    -webkit-filter: drop-shadow(0 0 3px #3b3b3b);
            filter: drop-shadow(0 0 3px #3b3b3b);
    height: calc(40px + 3vh);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    position: fixed;
    width: 100vw;
    top: 0;
    z-index: 999;
}
  
.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: inherit;
    width: 100%;
}

.navbar-logo {
    margin-left: 2vw;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    height: 100%;
}

.navbar-logo-img {
    height: 90%;
}

.fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    height: inherit;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-item {
    height: inherit;
}

.nav-links {
    color: rgb(90, 0, 0, 100);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    border-bottom: 4px solid rgb(90, 0, 0, 100);
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: rgb(90, 0, 0, 100);;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 800px) {
    .NavbarItems {
        position: relative;
    }

    .nav-item {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        width: 0;
        height: 94vh;
        margin-right: 0;
        position: fixed;
        top: calc(40px + 3vh);
        right: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: url(/jie-bakery-website/static/media/home-page-bg.96782d91.jpg);
        right: 0;
        width: 100%;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        color: rgb(90, 0, 0, 100);
        border-radius: 0;
    }

    .navbar-logo {
        display: flex;
        justify-self: flex-start;
        align-self: right;
        padding-right: 2%;
    }

    .menu-icon {
        display: flex;
        align-self: center;
        cursor: pointer;
        margin-right: 2rem;
        color: rgb(90, 0, 0, 100);
    }
}
