.feature-section-grid-element.end.a {
    align-items: center;
    justify-self: flex-start;
}
.feature-section-grid-element.end.b {
    align-items: center;
    justify-self: flex-end;
}

.feature-section-grid-element.mid {
    align-items: flex-end;
}

.feature-section-grid-element > img {
    max-width: 100%;
    max-height: 33vh;
}

.feature-section-grid-element.sm-logo {
    justify-content: center;
}

.feature-section-grid-element.sm-logo > img {
    max-width: 40%;
    max-height: 33vh;
    margin: 0;
}

.feature-section-grid-element > h1 {
    font-size: calc(12px + 3.0vw);
    font-weight: 500;
    font-family: "Times New Roman", Times, serif;
}

.feature-section-grid-element > h2 {
    font-size: calc(12px + 2.2vw);
    font-weight: 500;
    font-family: "Times New Roman", Times, serif;
}

.feature-section-grid.sm {
    margin: 5vh 12vw 18vh 12vw;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-column-gap: 1%;
    -webkit-column-gap: 1%;
            column-gap: 1%;
    grid-row-gap: 5%;
    row-gap: 5%;
}

@media screen and (max-width: 800px) {
    .feature-section-grid {
        margin: 5vh 12vw 10vh 12vw;
    }

    .feature-section-grid.sm {
        margin: 5vh 12vw 8vh 12vw;
        display: grid;
        grid-template-columns: auto auto;
        grid-column-gap: 1%;
        -webkit-column-gap: 1%;
                column-gap: 1%;
        grid-row-gap: 3%;
        row-gap: 3%;
    }

    .feature-section-grid.header-2 {
        grid-template-columns: 80% auto;
        grid-column-gap: 1%;
        -webkit-column-gap: 1%;
                column-gap: 1%;
    }

    .feature-section-grid-element.sm-logo {
        justify-content: flex-end;
    }

    .feature-section-grid-element.sm-logo > img {
        max-width: 80%;
        max-height: 33vh;
        margin: 0;
    }
}

.about-us-bottom {
    display: flex;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
}

.about-us-bottom > img {
    margin: 0;
    height: auto;
    width: 100%;
    object-fit: contain;
}

.about-us-page-container {
    background: url(/jie-bakery-website/static/media/about-us-background.318bb126.jpg);
    background-size: 100% 100%;
    margin-top: calc(40px + 3vh);
    width: 100vw;
}

